nav{
    background-color: black;
    padding: 10px 0;
}

nav ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
}

nav ul li{
    padding: 0 15px;
    display: inline-block;
}

nav ul li a{
    text-decoration: none;
    color: white;
}